$prefix: '.rise-blocks';
#{$prefix}-block-lists{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;   
}
$profile-size: 100px;

#{$prefix}-border-enable{
    #{$prefix}-block-lists{
        > div:not(.block-list-appender){
            transition: all 0.3s linear;
            border: 1px solid #ddd;
        }
    }
}

/* Layout 1 */
#{$prefix}-circle-layout{

    #{$prefix}-profile-card {
        padding: 2rem;
        padding-top: calc( $profile-size / 2 );
        
        #{$prefix}-description-wrapper{
            #{$prefix}-profile-card-designation{
                span{
                    border-color: transparent !important;
                }
            }
        }

        #{$prefix}-profile-card-image {  
            width: $profile-size;
            height: $profile-size;
            border-radius: 50%;
            margin-top: -$profile-size;
        }

        #{$prefix}-description-wrapper{
            display: flex;
            flex-direction: column;
        }

        #{$prefix}-profile-card-designation{
            position: relative;
            &:before{
                content: "-";
                margin-right: 7px;
            }

            > span{
                display: inline-block;
                max-width: 100%;
            }

        }

    }
}


/* Layout 2 */
#{$prefix}-rectangle-layout{

    #{$prefix}-profile-card {
        display: flex;
        flex-direction: row-reverse;


        #{$prefix}-profile-card-image{
            width: 35%;
        }

        #{$prefix}-description-wrapper{
            width: 65%;
            padding: 2rem;
            overflow-wrap: break-word;
        }

        #{$prefix}-profile-card-designation > span{
            max-width: 100%;
            padding: 0 0.4rem;
            border-radius: 0.2rem;
            display: inline-block;
        }
    }  
}
